<template>
    <div>
        <mhead-top titleHead="我的地址" lfIcon="el-icon-arrow-left icon" lfUrl="/userCenter/index/index" rgIcon="el-icon-s-home icon" rgUrl="/home"></mhead-top>
        <div class="mBg">
            <div v-if="tableData.length==0" class="f16 color6 tc mt30">暂无数据</div>
            <div v-else class="mu-add" v-for="(item,index) in tableData">
                <div class="mua-top flex f-nowrap">
                    <div class="name">{{item.name}}</div>
                    <div class="phone">{{item.phone}}</div>
                </div>
                <div class="mua-text">{{item.adddress}}</div>
                <div class="mua-control flex f-nowrap">
                    <div class="mua-default">
                        <el-switch
                                @change = "setDefault(item.add_id,item.is_default)"
                                :disabled="item.is_default"
                                v-model="item.is_default">
                        </el-switch>
                        <el-link class="ml5">默认地址</el-link>
                    </div>
                    <div class="mua-btn">
                        <el-button size="mini" @click="editAddress(item.add_id)">编辑</el-button>
                        <el-button size="mini" @click="delAddress(item.add_id)">删除</el-button>
                    </div>
                </div>
            </div>
            <div class="mu-addBtn" @click="addAddress()">
                <span>
                    <i class="el-icon-plus el-icon--left"></i>添加收货地址
                </span>
            </div>

        </div>
    </div>
</template>


<script>
    import mheadTop from '../../../components/mobile/headTop';
    import {mapState, mapMutations, mapActions} from 'vuex'
    import {address,defaultAddress,delAddress} from '../../../service/api'
    export default {
        name: 'addressWap',
        data(){
            return{
                dialogFormVisible: false,
                tableData: [],
                form: {
                    name: '',
                    region: '',
                    date1: '',
                    date2: '',
                    delivery: false,
                    type: [],
                    resource: '',
                    desc: ''
                },
                formLabelWidth: '90px',
                value: [],
                area: [
                    {
                    value: 'zhinan',
                    label: '指南',
                    children: [{
                        value: 'shejiyuanze',
                        label: '设计原则',
                        children: [{
                            value: 'yizhi',
                            label: '一致'
                        }, {
                            value: 'fankui',
                            label: '反馈'
                        }, {
                            value: 'xiaolv',
                            label: '效率'
                        }, {
                            value: 'kekong',
                            label: '可控'
                        }]
                    }, {
                        value: 'daohang',
                        label: '导航',
                        children: [{
                            value: 'cexiangdaohang',
                            label: '侧向导航'
                        }, {
                            value: 'dingbudaohang',
                            label: '顶部导航'
                        }]
                    }]
                }, {
                    value: 'zujian',
                    label: '组件',
                    children: [{
                        value: 'basic',
                        label: 'Basic',
                        children: [{
                            value: 'layout',
                            label: 'Layout 布局'
                        }, {
                            value: 'color',
                            label: 'Color 色彩'
                        }, {
                            value: 'typography',
                            label: 'Typography 字体'
                        }, {
                            value: 'icon',
                            label: 'Icon 图标'
                        }, {
                            value: 'button',
                            label: 'Button 按钮'
                        }]
                    }, {
                        value: 'form',
                        label: 'Form',
                        children: [{
                            value: 'radio',
                            label: 'Radio 单选框'
                        }, {
                            value: 'checkbox',
                            label: 'Checkbox 多选框'
                        }, {
                            value: 'input',
                            label: 'Input 输入框'
                        }, {
                            value: 'input-number',
                            label: 'InputNumber 计数器'
                        }, {
                            value: 'select',
                            label: 'Select 选择器'
                        }, {
                            value: 'cascader',
                            label: 'Cascader 级联选择器'
                        }, {
                            value: 'switch',
                            label: 'Switch 开关'
                        }, {
                            value: 'slider',
                            label: 'Slider 滑块'
                        }, {
                            value: 'time-picker',
                            label: 'TimePicker 时间选择器'
                        }, {
                            value: 'date-picker',
                            label: 'DatePicker 日期选择器'
                        }, {
                            value: 'datetime-picker',
                            label: 'DateTimePicker 日期时间选择器'
                        }, {
                            value: 'upload',
                            label: 'Upload 上传'
                        }, {
                            value: 'rate',
                            label: 'Rate 评分'
                        }, {
                            value: 'form',
                            label: 'Form 表单'
                        }]
                    }, {
                        value: 'data',
                        label: 'Data',
                        children: [{
                            value: 'table',
                            label: 'Table 表格'
                        }, {
                            value: 'tag',
                            label: 'Tag 标签'
                        }, {
                            value: 'progress',
                            label: 'Progress 进度条'
                        }, {
                            value: 'tree',
                            label: 'Tree 树形控件'
                        }, {
                            value: 'pagination',
                            label: 'Pagination 分页'
                        }, {
                            value: 'badge',
                            label: 'Badge 标记'
                        }]
                    }, {
                        value: 'notice',
                        label: 'Notice',
                        children: [{
                            value: 'alert',
                            label: 'Alert 警告'
                        }, {
                            value: 'loading',
                            label: 'Loading 加载'
                        }, {
                            value: 'message',
                            label: 'Message 消息提示'
                        }, {
                            value: 'message-box',
                            label: 'MessageBox 弹框'
                        }, {
                            value: 'notification',
                            label: 'Notification 通知'
                        }]
                    }, {
                        value: 'navigation',
                        label: 'Navigation',
                        children: [{
                            value: 'menu',
                            label: 'NavMenu 导航菜单'
                        }, {
                            value: 'tabs',
                            label: 'Tabs 标签页'
                        }, {
                            value: 'breadcrumb',
                            label: 'Breadcrumb 面包屑'
                        }, {
                            value: 'dropdown',
                            label: 'Dropdown 下拉菜单'
                        }, {
                            value: 'steps',
                            label: 'Steps 步骤条'
                        }]
                    }, {
                        value: 'others',
                        label: 'Others',
                        children: [{
                            value: 'dialog',
                            label: 'Dialog 对话框'
                        }, {
                            value: 'tooltip',
                            label: 'Tooltip 文字提示'
                        }, {
                            value: 'popover',
                            label: 'Popover 弹出框'
                        }, {
                            value: 'card',
                            label: 'Card 卡片'
                        }, {
                            value: 'carousel',
                            label: 'Carousel 走马灯'
                        }, {
                            value: 'collapse',
                            label: 'Collapse 折叠面板'
                        }]
                    }]
                }, {
                    value: 'ziyuan',
                    label: '资源',
                    children: [{
                        value: 'axure',
                        label: 'Axure Components'
                    }, {
                        value: 'sketch',
                        label: 'Sketch Templates'
                    }, {
                        value: 'jiaohu',
                        label: '组件交互文档'
                    }]
                }
            ]
            }
        },
        components:{
            mheadTop
        },
        mounted(){
            this.initData();
        },
        computed:{
            ...mapState([
                'login'
            ])

        },
        methods:{
            async initData(){
                // 获取列表
                var res = await address();
                if(res){
                    if(res.status != 12007){
                        this.tableData = await address();
                    }else{
                        this.$message({
                            message: res.msg,
                            type: 'warning'
                        });
                    }
                }
            },
            async editAddress(add_id){
                this.$router.push({path:"/userCenter/address/add",query:{add_id:add_id}});
            },
            async delAddress(add_id){
                this.$confirm('确定要删除该地址?', '', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'info'
                }).then(async () => {
                    var res = await delAddress(
                        add_id
                    );
                    if(res){
                        this.$message({
                            message: res.msg,
                            type: res.status==12000?'success':'warning'
                        });
                        if(res.status == 12000){
                            this.tableData = await address();
                        }
                    }
                }).catch(async () => {
                    //this.tableData = await address();
                });
            },
            setDefault(add_id,is_default){
                if(is_default == true){
                    this.$confirm('确定要将此条地址设为默认地址?', '', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'info'
                    }).then(async () => {
                        var res = await defaultAddress(
                            add_id
                        );
                        if(res){
                            this.$message({
                                message: res.msg,
                                type: res.status==12000?'success':'warning'
                            });
                            if(res.status == 12000){
                                this.tableData = await address();
                            }
                        }
                    }).catch(async () => {
                        this.tableData = await address();
                    });
                }

            },
            addAddress(){
                if(this.tableData.length>=6){
                    this.$message({
                        message: '地址已达到上限，不能超过6条哦',
                        type: 'warning'
                    });
                }else{
                    this.$router.push({ path: '/userCenter/address/add' })
                }
            }
        },
        created(){

        },

    }

</script>

<style lang="scss">
    @import '../../../style/mixin';
    .mBg{
        background-color:$bggray;
        display: block;
        width:100%;
        min-height: px2rem(1140);
        padding:px2rem(90) 0 px2rem(104) 0;
    }
    .mu-add{
        background-color: #fff;
        padding:0 px2rem(20);
        margin-top:px2rem(20);
    }
    .mua-top{
        justify-content: space-between;
        padding:px2rem(20) 0 px2rem(10) 0;
    }
    .mua-text{
        padding-bottom: px2rem(20);
    }
    .mua-control{
        justify-content: space-between;
        padding:px2rem(20) 0;
        border-top:1px solid $bc;
    }
    .mu-addBtn{
        display: flex;
        flex-wrap:nowrap;
        height: px2rem(104);
        line-height: px2rem(104);
        background-color: #fff;
        position: fixed;
        left:0;
        bottom:0;
        width: 100%;
        border-top:1px solid #eee;
    }
    .mu-addBtn span{
        background-color: $default;
        color:#fff;
        width:100%;
        text-align: center;
        font-size: px2rem(32);
    }
</style>



